import React from 'react'
import { useState } from 'react'
import {AiOutlinePlusCircle} from 'react-icons/ai'


function SingleSurah({name}) {
  const [tasbeeh, setTasbeeh] = useState(0)
  return (
    <article className='tasbeeh'>
    <div className='img-container'>
      <h1>{tasbeeh}</h1>
    </div>
    <div className='content-container'>
      <h3>{name}</h3>
      <AiOutlinePlusCircle className='plus-icon' onClick={()=> setTasbeeh(tasbeeh+1)} />
      <button className='reset-btn' onClick={()=> setTasbeeh(0)}>Reset Now</button>
    </div>
    
  </article>
  )
}

export default SingleSurah