import React, {useState} from 'react';
import SingleSurah from './comps/SingleSurah';
import PopupBox from './comps/PopupBox';
import AddButton from './comps/AddButton'
function App() {
  const [tasbeehatArray, setTasbeehatArray] = useState([{name: "100 darood"},{name: "300 darood"},{name:"500 darood",description: "ldkjflajdl"}])
  const [popupDisplay, setPopupDisplay] = useState(false)
  const addTasbeeh = (name,description)=>{
      
    setTasbeehatArray(()=>{
      const newArray = tasbeehatArray;
      newArray.push({
        name: name,
        description: description
  
      })
      console.log(newArray)
      return newArray
    })
  }
  return (
    <main>
      {popupDisplay !== false && <PopupBox addTasbeeh={addTasbeeh} setPopupDisplay={setPopupDisplay} />}
     <span className="bottom-side" onClick={()=> setPopupDisplay(true)}>
     <AddButton />
     </span>
      <section className='container'>
        <div className='title'>
          <h2>Tasbeehat</h2>
          <div className='underline'></div>
        </div>
        <div className='tasbeehat-container'>
          {tasbeehatArray.length === 0 ? "Not Found" : Array.from(tasbeehatArray).map((item)=>{
        return <SingleSurah name={item.name}/>
          })}
         
        </div>
      </section>
    </main>
  );
}

export default App;
