import React,{useState} from 'react'
import AddButton from './AddButton'

function PopupBox({addTasbeeh,setPopupDisplay}) {
  const [name, setName] = useState()
  const [description, setDescription] = useState()
  return <div className='black-wall'>
          <div className='popup-content-container'>
                    <h3>Add Require Information Below</h3>
                    <div className='underline'></div>
                    <form>
                              <input placeholder='Enter Tasbeeh Name Here' onChange={(e)=> setName(e.target.value)} />
                              <textarea placeholder="Let's Add Some Description" className='form-description-field' onChange={((e)=> setDescription(e.target.value))} />
                              <span onClick={()=>{
                                setPopupDisplay(false)
                                addTasbeeh(name,description)
                                }}>
                                        <AddButton/>
                              </span>
                    </form>
          </div>
    </div>
  
}

export default PopupBox